'use server';
/**
 * @fileOverview Suggests keywords for user insights using AI.
 *
 * - suggestInsightKeywords - A function that suggests keywords for a given insight text.
 * - SuggestInsightKeywordsInput - The input type for the suggestInsightKeywords function.
 * - SuggestInsightKeywordsOutput - The return type for the suggestInsightKeywords function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const SuggestInsightKeywordsInputSchema = z.object({
  insightText: z.string().describe("The user's insight text to analyze for keywords."),
});
export type SuggestInsightKeywordsInput = z.infer<typeof SuggestInsightKeywordsInputSchema>;

const SuggestInsightKeywordsOutputSchema = z.object({
  keywords: z.array(z.string()).describe('A list of suggested keywords or short phrases (3-5 items).'),
});
export type SuggestInsightKeywordsOutput = z.infer<typeof SuggestInsightKeywordsOutputSchema>;

export async function suggestInsightKeywords(input: SuggestInsightKeywordsInput): Promise<SuggestInsightKeywordsOutput> {
  return suggestInsightKeywordsFlow(input);
}

const suggestKeywordsPrompt = ai.definePrompt({
  name: 'suggestInsightKeywordsPrompt',
  input: {schema: SuggestInsightKeywordsInputSchema},
  output: {schema: SuggestInsightKeywordsOutputSchema},
  prompt: `You are an AI assistant helping users reflect on their goals and insights.
Given the following user's insight about overcoming a potential failure related to their goal, suggest 3-5 concise keywords or short phrases.
These keywords should capture the main themes, actionable elements, or core realizations from the insight.
Ensure the output is a list of strings.

User Insight:
{{{insightText}}}

Suggested Keywords:`,
  config: {
    temperature: 0.5, 
  }
});

const suggestInsightKeywordsFlow = ai.defineFlow(
  {
    name: 'suggestInsightKeywordsFlow',
    inputSchema: SuggestInsightKeywordsInputSchema,
    outputSchema: SuggestInsightKeywordsOutputSchema,
  },
  async (input) => {
    const {output} = await suggestKeywordsPrompt(input);
    if (!output || !Array.isArray(output.keywords)) {
        console.warn("LLM did not return expected keywords format. Output:", output);
        return { keywords: [] };
    }
    return output;
  }
);
