
'use client';

import { useState, useEffect, useCallback } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Slider } from '@/components/ui/slider';
import { Badge } from '@/components/ui/badge';
import { useToast } from '@/hooks/use-toast';
import type { JournalEntry } from '@/types';
import { suggestInsightKeywords } from '@/ai/flows/suggest-insight-keywords-flow';
import { categorizeFailureReason, type CategorizeFailureReasonOutput } from '@/ai/flows/categorize-failure-reason-flow';
import { Loader2, Send, Lightbulb, AlertTriangle, CheckCircle, XCircle, MessageSquareHeart, Sparkles, Info, Target, ShieldCheck, RefreshCw, Heart, Star, ArrowLeft, ListChecks, Sun, Award, ListOrdered, HelpCircle, Workflow, Triangle, Eye, MessagesSquare, Users, Wand } from 'lucide-react';
import { cn } from '@/lib/utils';
import Link from 'next/link';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';


const JOURNAL_HISTORY_KEY = 'reflectifyJournalHistory';
const FOCUSED_GOAL_VISION_ID_KEY = 'reflectifyCurrentFocusedGoalVisionId';
const FOCUSED_GOAL_VISION_TYPE_KEY = 'reflectifyCurrentFocusedGoalVisionType';

type ProcessType = 'impossibleToFail' | 'goalDesirability' | 'bestCaseScenario' | 'goalAchievementExperience' | 'bestCasePlusActions' | 'actionsCertainty' | 'detailedActionProcess' | 'arcTriangle' | 'emt';

export default function ProcessingPage() {
  const [selectedProcessType, setSelectedProcessType] = useState<ProcessType | null>(null);

  // States for 'impossibleToFail'
  const [canGoalFail, setCanGoalFail] = useState<boolean | null>(null);
  const [failureReason, setFailureReason] = useState<string>('');
  const [leftHandText, setLeftHandText] = useState<string>('');
  const [rightHandText, setRightHandText] = useState<string>('');
  const [categorizedFailureOutput, setCategorizedFailureOutput] = useState<CategorizeFailureReasonOutput | null>(null);
  const [showProcessingResults, setShowProcessingResults] = useState<boolean>(false);
  const [isProcessingFailure, setIsProcessingFailure] = useState<boolean>(false);

  // States for 'goalDesirability'
  const [goalWantRating, setGoalWantRating] = useState<number>(10);
  const [whyNotTenRatingReason, setWhyNotTenRatingReason] = useState<string>('');
  const [goalWantReasons, setGoalWantReasons] = useState<string>('');

  // States for 'bestCaseScenario' & 'bestCasePlusActions'
  const [bestCaseScenarioText, setBestCaseScenarioText] = useState<string>('');
  
  // States for 'goalAchievementExperience'
  const [goalAchievementExperienceText, setGoalAchievementExperienceText] = useState<string>('');

  // States for 'bestCasePlusActions'
  const [actionsListText, setActionsListText] = useState<string>('');
  // actionsCertaintyWhyNotText is shared with 'actionsCertainty'

  // States for 'actionsCertainty' & 'bestCasePlusActions'
  const [actionsCertaintyWhyNotText, setActionsCertaintyWhyNotText] = useState<string>('');

  // States for 'detailedActionProcess'
  const [actionFeelingInitial, setActionFeelingInitial] = useState<string>('');
  const [actionFeelingDesired, setActionFeelingDesired] = useState<string>('');
  const [actionCharacteristicsNeeded, setActionCharacteristicsNeeded] = useState<string>('');
  const [actionCharacteristicsWithout, setActionCharacteristicsWithout] = useState<string>('');
  const [actionCharacteristicsWith, setActionCharacteristicsWith] = useState<string>('');
  const [actionUnderstandingNeeded, setActionUnderstandingNeeded] = useState<string>('');
  const [actionUnderstandingWithout, setActionUnderstandingWithout] = useState<string>('');
  const [actionUnderstandingWith, setActionUnderstandingWith] = useState<string>('');

  // States for 'arcTriangle'
  const [arcAffinityMyRating, setArcAffinityMyRating] = useState<number>(10);
  const [arcAffinityGoalRating, setArcAffinityGoalRating] = useState<number>(10);
  const [arcAffinityWhyNot, setArcAffinityWhyNot] = useState<string>('');
  const [arcAffinityExperienceInstead, setArcAffinityExperienceInstead] = useState<string>('');
  const [arcAffinityRealizations, setArcAffinityRealizations] = useState<string>('');

  const [arcRealityMyAgreementRating, setArcRealityMyAgreementRating] = useState<number>(10);
  const [arcRealityFateAgreementRating, setArcRealityFateAgreementRating] = useState<number>(10);
  const [arcRealityWhyNot, setArcRealityWhyNot] = useState<string>('');
  const [arcRealityExperienceInstead, setArcRealityExperienceInstead] = useState<string>('');
  const [arcRealityRealizations, setArcRealityRealizations] = useState<string>('');

  const [arcCommunicationOutflowRating, setArcCommunicationOutflowRating] = useState<number>(10);
  const [arcCommunicationInflowRating, setArcCommunicationInflowRating] = useState<number>(10);
  const [arcCommunicationWhyNot, setArcCommunicationWhyNot] = useState<string>('');
  const [arcCommunicationExperienceInstead, setArcCommunicationExperienceInstead] = useState<string>('');
  const [arcCommunicationRealizations, setArcCommunicationRealizations] = useState<string>('');
  
  // States for 'emt' (Experience Modification Technique)
  const [emtExperienceDescription, setEmtExperienceDescription] = useState<string>('');
  const [emtInitialRating, setEmtInitialRating] = useState<number>(0);
  const [emtPositiveParts, setEmtPositiveParts] = useState<string>('');
  const [emtNegativeParts, setEmtNegativeParts] = useState<string>('');
  const [emtNeedsDifferentBetter, setEmtNeedsDifferentBetter] = useState<string>('');
  const [emtModifiedRating, setEmtModifiedRating] = useState<number>(0);
  const [emtNeedsDifferentEvenBetter, setEmtNeedsDifferentEvenBetter] = useState<string>('');
  const [emtHappenedBefore, setEmtHappenedBefore] = useState<string>('');
  const [emtExperienceNow, setEmtExperienceNow] = useState<string>('');
  const [emtFinalRating, setEmtFinalRating] = useState<number>(0);

  // Common states
  const [insightsText, setInsightsText] = useState<string>('');
  const [goalImprovementText, setGoalImprovementText] = useState<string>('');
  const [journalHistory, setJournalHistory] = useState<JournalEntry[]>([]);
  const [isLoadingAi, setIsLoadingAi] = useState<boolean>(false); 
  const [suggestedKeywords, setSuggestedKeywords] = useState<string[] | null>(null);
  const [isSuggestingKeywords, setIsSuggestingKeywords] = useState<boolean>(false);
  
  const { toast } = useToast();


  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(JOURNAL_HISTORY_KEY);
      if (storedHistory) {
        const parsedHistory: JournalEntry[] = JSON.parse(storedHistory);
        // Filter for entries that have any of the processing-specific fields populated
        const processingEntries = parsedHistory.filter(
          entry => entry.isFailProof === true || 
                   (entry.failureReason && entry.failureReason.trim() !== '') ||
                   entry.goalWantRating !== undefined ||
                   (entry.bestCaseScenarioText && entry.bestCaseScenarioText.trim() !== '') ||
                   (entry.goalAchievementExperienceText && entry.goalAchievementExperienceText.trim() !== '') ||
                   (entry.actionsListText && entry.actionsListText.trim() !== '') ||
                   (entry.actionsCertaintyWhyNotText && entry.actionsCertaintyWhyNotText.trim() !== '') ||
                   (entry.actionFeelingInitial && entry.actionFeelingInitial.trim() !== '') || 
                   entry.arcAffinityMyRating !== undefined ||
                   entry.emtExperienceDescription !== undefined // Check for EMT data
        );
        setJournalHistory(processingEntries);
      }
    } catch (error) {
      console.error("Failed to load or filter journal history from localStorage:", error);
      toast({
        title: "Error",
        description: "Could not load your journal history.",
        variant: "destructive",
      });
    }
  }, [toast]);


  const handleSetCanGoalFail = (value: boolean) => {
    setCanGoalFail(value);
    setSuggestedKeywords(null); 
    setShowProcessingResults(false);
    setCategorizedFailureOutput(null);
    setLeftHandText('');
    setRightHandText('');

    if (value === false) {
      setFailureReason(''); 
      toast({
        title: "Goal Marked as Fail-Proof",
        description: "Standard 10F Process section has been hidden. You now know it is impossible to fail.",
        variant: "default",
      });
    }
  };


  const handleFailureProcessing = useCallback(async () => {
    if (!failureReason.trim()) {
      toast({
        title: 'Missing Information',
        description: 'Please describe how your goal could potentially fail.',
        variant: 'default',
      });
      return;
    }
    setIsProcessingFailure(true);
    setSuggestedKeywords(null);
    setCategorizedFailureOutput(null);
    setShowProcessingResults(false);

    try {
      const result = await categorizeFailureReason({ failureReason });
      setCategorizedFailureOutput(result);
      setLeftHandText(`${result.categorizedStatement}, inhale exhale.`);
      setRightHandText(`Although ${result.categorizedStatement}, I still want my goal.`);
      setShowProcessingResults(true);
      toast({
        title: 'Processing Complete',
        description: '10F processing results are now available. Please reflect on your insights.',
      });
    } catch (error) {
      console.error('Error categorizing failure reason:', error);
      setLeftHandText(`It is possible to fail because ${failureReason}, inhale exhale.`);
      setRightHandText(`Although it is possible to fail because ${failureReason}, I still want my goal.`);
      setShowProcessingResults(true);
      toast({
        title: 'Processing Note',
        description: 'Could not categorize failure reason with AI, using basic processing. Please reflect on your insights.',
        variant: 'default',
      });
    } finally {
      setIsProcessingFailure(false);
    }
  }, [failureReason, toast]);

  const handleSuggestKeywords = useCallback(async () => {
    if (!insightsText.trim()) {
      toast({
        title: 'Empty Insight',
        description: 'Please write your insights before suggesting keywords.',
        variant: 'default',
      });
      return;
    }
    setIsSuggestingKeywords(true);
    setSuggestedKeywords(null);
    try {
      const result = await suggestInsightKeywords({ insightText: insightsText });
      setSuggestedKeywords(result.keywords);
      toast({
        title: 'Keywords Suggested',
        description: 'AI has suggested some keywords for your insight.',
      });
    } catch (error) {
      console.error('Error suggesting keywords:', error);
      toast({
        title: 'Keyword Suggestion Error',
        description: 'Could not get keyword suggestions. Please try again.',
        variant: "destructive",
      });
    } finally {
      setIsSuggestingKeywords(false);
    }
  }, [insightsText, toast]);

  const handleJournalSubmit = useCallback(async () => {
    if (selectedProcessType === null) {
        toast({ title: 'No Process Selected', description: 'Please select a process to work on.', variant: 'default' });
        return;
    }

    setIsLoadingAi(true);
    try {
      const newEntry: Partial<JournalEntry> = { 
        id: new Date().toISOString(),
        date: new Date().toISOString(),
        insightsText: insightsText.trim() ? insightsText.trim() : undefined,
        goalImprovementText: goalImprovementText.trim() ? goalImprovementText.trim() : undefined,
      };

      if (selectedProcessType === 'impossibleToFail') {
        if (canGoalFail === null) {
            toast({ title: 'Incomplete Entry', description: 'Please select if your goal can potentially fail.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.isFailProof = canGoalFail === false ? true : undefined;
        if (canGoalFail === true) {
            if (!failureReason.trim() && showProcessingResults) {
                 toast({ title: 'Incomplete Entry', description: 'Please describe how your goal could potentially fail if you have processed it.', variant: 'default' });
                 setIsLoadingAi(false); return;
            }
            if (showProcessingResults && !insightsText.trim()) {
                 toast({ title: 'Missing Insights', description: 'Please provide your insights and realizations from the failure processing.', variant: 'default' });
                 setIsLoadingAi(false); return;
            }
            newEntry.failureReason = failureReason.trim() ? failureReason.trim() : undefined;
            newEntry.leftHandText = leftHandText.trim() && showProcessingResults ? leftHandText.trim() : undefined;
            newEntry.rightHandText = rightHandText.trim() && showProcessingResults ? rightHandText.trim() : undefined;
            newEntry.failureCategory = showProcessingResults ? categorizedFailureOutput?.category : undefined;
            newEntry.categorizedFailureStatement = showProcessingResults ? categorizedFailureOutput?.categorizedStatement : undefined;
        }
      } else if (selectedProcessType === 'goalDesirability') {
        if (goalWantRating < 10 && !whyNotTenRatingReason.trim()) {
            toast({ title: 'Missing Information', description: 'Please explain why your goal desirability is not a 10/10.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        if (goalWantRating < 10 && !goalWantReasons.trim()){
            toast({ title: 'Missing Information', description: 'Please state your reasons for wanting this goal, as your desirability rating is less than 10/10.', variant: 'default'});
            setIsLoadingAi(false); return;
        }
        if (goalWantRating < 10 && (whyNotTenRatingReason.trim() || goalWantReasons.trim()) && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for the goal desirability process.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.goalWantRating = goalWantRating;
        newEntry.whyNotTenRatingReason = goalWantRating < 10 ? whyNotTenRatingReason.trim() : undefined;
        newEntry.goalWantReasons = goalWantRating < 10 ? goalWantReasons.trim() : undefined;
      } else if (selectedProcessType === 'bestCaseScenario') {
        if (!bestCaseScenarioText.trim()) {
            toast({ title: 'Incomplete Entry', description: 'Please describe your best case scenario.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        if (bestCaseScenarioText.trim() && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for the best case scenario.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.bestCaseScenarioText = bestCaseScenarioText.trim();
        localStorage.setItem(FOCUSED_GOAL_VISION_ID_KEY, newEntry.id!);
        localStorage.setItem(FOCUSED_GOAL_VISION_TYPE_KEY, 'bestCaseScenario');
      } else if (selectedProcessType === 'goalAchievementExperience') {
        if (!goalAchievementExperienceText.trim()) {
            toast({ title: 'Incomplete Entry', description: 'Please describe your goal achievement experience.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        if (goalAchievementExperienceText.trim() && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for the goal achievement experience.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.goalAchievementExperienceText = goalAchievementExperienceText.trim();
        localStorage.setItem(FOCUSED_GOAL_VISION_ID_KEY, newEntry.id!);
        localStorage.setItem(FOCUSED_GOAL_VISION_TYPE_KEY, 'goalAchievementExperience');
      } else if (selectedProcessType === 'bestCasePlusActions') {
        if (!bestCaseScenarioText.trim() || !actionsListText.trim() || !actionsCertaintyWhyNotText.trim()) {
             toast({ title: 'Incomplete Entry', description: 'Please fill all fields for the Best Case + Actions.', variant: 'default' });
             setIsLoadingAi(false); return;
        }
        if ((bestCaseScenarioText.trim() || actionsListText.trim() || actionsCertaintyWhyNotText.trim()) && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for this process.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.bestCaseScenarioText = bestCaseScenarioText.trim();
        newEntry.actionsListText = actionsListText.trim();
        newEntry.actionsCertaintyWhyNotText = actionsCertaintyWhyNotText.trim();
      } else if (selectedProcessType === 'actionsCertainty') {
        if (!actionsCertaintyWhyNotText.trim()) {
            toast({ title: 'Incomplete Entry', description: 'Please fill the field for the Actions Certainty.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        if (actionsCertaintyWhyNotText.trim() && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for this process.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.actionsCertaintyWhyNotText = actionsCertaintyWhyNotText.trim();
      } else if (selectedProcessType === 'detailedActionProcess') {
        if (!actionFeelingInitial.trim() && !actionFeelingDesired.trim() && !actionCharacteristicsNeeded.trim() && !actionCharacteristicsWithout.trim() && !actionCharacteristicsWith.trim() && !actionUnderstandingNeeded.trim() && !actionUnderstandingWithout.trim() && !actionUnderstandingWith.trim() ) {
             toast({ title: 'Incomplete Entry', description: 'Please fill at least one field for the Detailed Action Analysis.', variant: 'default' });
             setIsLoadingAi(false); return;
        }
         if ((actionFeelingInitial.trim() || actionFeelingDesired.trim() || actionCharacteristicsNeeded.trim() || actionCharacteristicsWithout.trim() || actionCharacteristicsWith.trim() || actionUnderstandingNeeded.trim() || actionUnderstandingWithout.trim() || actionUnderstandingWith.trim()) && !insightsText.trim()) {
            toast({ title: 'Missing Insights', description: 'Please provide your insights for this process.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        newEntry.actionFeelingInitial = actionFeelingInitial.trim() ? actionFeelingInitial.trim() : undefined;
        newEntry.actionFeelingDesired = actionFeelingDesired.trim() ? actionFeelingDesired.trim() : undefined;
        newEntry.actionCharacteristicsNeeded = actionCharacteristicsNeeded.trim() ? actionCharacteristicsNeeded.trim() : undefined;
        newEntry.actionCharacteristicsWithout = actionCharacteristicsWithout.trim() ? actionCharacteristicsWithout.trim() : undefined;
        newEntry.actionCharacteristicsWith = actionCharacteristicsWith.trim() ? actionCharacteristicsWith.trim() : undefined;
        newEntry.actionUnderstandingNeeded = actionUnderstandingNeeded.trim() ? actionUnderstandingNeeded.trim() : undefined;
        newEntry.actionUnderstandingWithout = actionUnderstandingWithout.trim() ? actionUnderstandingWithout.trim() : undefined;
        newEntry.actionUnderstandingWith = actionUnderstandingWith.trim() ? actionUnderstandingWith.trim() : undefined;
      } else if (selectedProcessType === 'arcTriangle') {
        const isAffinityPartiallyFilled = arcAffinityWhyNot.trim() || arcAffinityExperienceInstead.trim() || arcAffinityRealizations.trim();
        const isRealityPartiallyFilled = arcRealityWhyNot.trim() || arcRealityExperienceInstead.trim() || arcRealityRealizations.trim();
        const isCommunicationPartiallyFilled = arcCommunicationWhyNot.trim() || arcCommunicationExperienceInstead.trim() || arcCommunicationRealizations.trim();

        if ( (arcAffinityMyRating < 10 || arcAffinityGoalRating < 10) && !arcAffinityWhyNot.trim() ) {
            toast({ title: 'Missing ARC Info', description: 'Please explain why Affinity ratings are not 10/10.', variant: 'default' }); setIsLoadingAi(false); return;
        }
        if ( (arcRealityMyAgreementRating < 10 || arcRealityFateAgreementRating < 10) && !arcRealityWhyNot.trim() ) {
            toast({ title: 'Missing ARC Info', description: 'Please explain why Reality ratings are not 10/10.', variant: 'default' }); setIsLoadingAi(false); return;
        }
        if ( (arcCommunicationOutflowRating < 10 || arcCommunicationInflowRating < 10) && !arcCommunicationWhyNot.trim() ) {
            toast({ title: 'Missing ARC Info', description: 'Please explain why Communication ratings are not 10/10.', variant: 'default' }); setIsLoadingAi(false); return;
        }

        if ( (isAffinityPartiallyFilled || isRealityPartiallyFilled || isCommunicationPartiallyFilled) && 
             !insightsText.trim() && !arcAffinityRealizations.trim() && !arcRealityRealizations.trim() && !arcCommunicationRealizations.trim() ) {
            toast({ title: 'Missing Insights', description: 'Please provide your realizations for the ARC Triangle or general insights.', variant: 'default' });
            setIsLoadingAi(false); return;
        }

        newEntry.arcAffinityMyRating = arcAffinityMyRating;
        newEntry.arcAffinityGoalRating = arcAffinityGoalRating;
        newEntry.arcAffinityWhyNot = (arcAffinityMyRating < 10 || arcAffinityGoalRating < 10) ? arcAffinityWhyNot.trim() : undefined;
        newEntry.arcAffinityExperienceInstead = arcAffinityExperienceInstead.trim() ? arcAffinityExperienceInstead.trim() : undefined;
        newEntry.arcAffinityRealizations = arcAffinityRealizations.trim() ? arcAffinityRealizations.trim() : undefined;

        newEntry.arcRealityMyAgreementRating = arcRealityMyAgreementRating;
        newEntry.arcRealityFateAgreementRating = arcRealityFateAgreementRating;
        newEntry.arcRealityWhyNot = (arcRealityMyAgreementRating < 10 || arcRealityFateAgreementRating < 10) ? arcRealityWhyNot.trim() : undefined;
        newEntry.arcRealityExperienceInstead = arcRealityExperienceInstead.trim() ? arcRealityExperienceInstead.trim() : undefined;
        newEntry.arcRealityRealizations = arcRealityRealizations.trim() ? arcRealityRealizations.trim() : undefined;

        newEntry.arcCommunicationOutflowRating = arcCommunicationOutflowRating;
        newEntry.arcCommunicationInflowRating = arcCommunicationInflowRating;
        newEntry.arcCommunicationWhyNot = (arcCommunicationOutflowRating < 10 || arcCommunicationInflowRating < 10) ? arcCommunicationWhyNot.trim() : undefined;
        newEntry.arcCommunicationExperienceInstead = arcCommunicationExperienceInstead.trim() ? arcCommunicationExperienceInstead.trim() : undefined;
        newEntry.arcCommunicationRealizations = arcCommunicationRealizations.trim() ? arcCommunicationRealizations.trim() : undefined;
      } else if (selectedProcessType === 'emt') {
        if (!emtExperienceDescription.trim()) {
            toast({ title: 'Incomplete EMT Process', description: 'Please describe the experience.', variant: 'default' });
            setIsLoadingAi(false); return;
        }
        if ((emtExperienceDescription.trim() || emtPositiveParts.trim() || emtNegativeParts.trim()) && !insightsText.trim()) {
             toast({ title: 'Missing Insights', description: 'Please provide your insights for this EMT process.', variant: 'default' });
             setIsLoadingAi(false); return;
        }
        newEntry.emtExperienceDescription = emtExperienceDescription.trim();
        newEntry.emtInitialRating = emtInitialRating;
        newEntry.emtPositiveParts = emtPositiveParts.trim() ? emtPositiveParts.trim() : undefined;
        newEntry.emtNegativeParts = emtNegativeParts.trim() ? emtNegativeParts.trim() : undefined;
        newEntry.emtNeedsDifferentBetter = emtNeedsDifferentBetter.trim() ? emtNeedsDifferentBetter.trim() : undefined;
        newEntry.emtModifiedRating = emtModifiedRating;
        newEntry.emtNeedsDifferentEvenBetter = emtNeedsDifferentEvenBetter.trim() ? emtNeedsDifferentEvenBetter.trim() : undefined;
        newEntry.emtHappenedBefore = emtHappenedBefore.trim() ? emtHappenedBefore.trim() : undefined;
        newEntry.emtExperienceNow = emtExperienceNow.trim() ? emtExperienceNow.trim() : undefined;
        newEntry.emtFinalRating = emtFinalRating;
      }
      
      newEntry.entryText = ''; 

      setJournalHistory(prevHistory => {
         const updatedHistory = [newEntry as JournalEntry, ...prevHistory.filter(
            entry => entry.isFailProof === true || 
                     (entry.failureReason && entry.failureReason.trim() !== '') || 
                     entry.goalWantRating !== undefined ||
                     (entry.bestCaseScenarioText && entry.bestCaseScenarioText.trim() !== '') ||
                     (entry.goalAchievementExperienceText && entry.goalAchievementExperienceText.trim() !== '') ||
                     (entry.actionsListText && entry.actionsListText.trim() !== '') ||
                     (entry.actionsCertaintyWhyNotText && entry.actionsCertaintyWhyNotText.trim() !== '') ||
                     (entry.actionFeelingInitial && entry.actionFeelingInitial.trim() !== '') ||
                     entry.arcAffinityMyRating !== undefined ||
                     entry.emtExperienceDescription !== undefined
        )];
        try {
          localStorage.setItem(JOURNAL_HISTORY_KEY, JSON.stringify(updatedHistory));
        } catch (error) {
          console.error("Failed to save journal history to localStorage:", error);
          toast({ title: "Storage Error", description: "Could not save your journal entry due to storage limitations.", variant: "destructive" });
        }
        return updatedHistory;
      });

      resetAllFields();

      toast({
        title: 'Progress Saved',
        description: 'Your processing details have been recorded.',
      });
    } catch (error) {
      console.error('Error saving processing entry:', error);
      toast({ title: 'Error', description: 'Could not save your processing entry. Please try again.', variant: "destructive" });
    } finally {
      setIsLoadingAi(false);
    }
  }, [
    toast, selectedProcessType, canGoalFail, failureReason, leftHandText, rightHandText, insightsText, 
    goalImprovementText, showProcessingResults, categorizedFailureOutput, 
    goalWantRating, whyNotTenRatingReason, goalWantReasons, bestCaseScenarioText, goalAchievementExperienceText,
    actionsListText, actionsCertaintyWhyNotText, actionFeelingInitial, actionFeelingDesired, 
    actionCharacteristicsNeeded, actionCharacteristicsWithout, actionCharacteristicsWith,
    actionUnderstandingNeeded, actionUnderstandingWithout, actionUnderstandingWith,
    arcAffinityMyRating, arcAffinityGoalRating, arcAffinityWhyNot, arcAffinityExperienceInstead, arcAffinityRealizations,
    arcRealityMyAgreementRating, arcRealityFateAgreementRating, arcRealityWhyNot, arcRealityExperienceInstead, arcRealityRealizations,
    arcCommunicationOutflowRating, arcCommunicationInflowRating, arcCommunicationWhyNot, arcCommunicationExperienceInstead, arcCommunicationRealizations,
    emtExperienceDescription, emtInitialRating, emtPositiveParts, emtNegativeParts, emtNeedsDifferentBetter, emtModifiedRating,
    emtNeedsDifferentEvenBetter, emtHappenedBefore, emtExperienceNow, emtFinalRating
  ]);

  const getCategoryDescription = (category?: string) => {
    if (!category) return null;
    switch (category) {
      case 'POSSIBLE_OUTCOME':
        return 'This failure reason seems to relate to a potential external event or outcome.';
      case 'PAST_TRAIT':
        return 'This failure reason seems to relate to a personal trait or past behavior.';
      case 'ABSOLUTE_TRUTH':
        return 'This failure reason seems to relate to a perceived unchangeable fact or limiting belief.';
      default:
        return null;
    }
  };
  
  const resetAllFields = (keepProcessSelection = false) => {
    if (!keepProcessSelection) {
        setSelectedProcessType(null);
    }
    // Impossible to Fail
    setCanGoalFail(null);
    setFailureReason('');
    setLeftHandText('');
    setRightHandText('');
    setShowProcessingResults(false);
    setCategorizedFailureOutput(null);

    // Goal Desirability
    setGoalWantRating(10);
    setWhyNotTenRatingReason('');
    setGoalWantReasons('');

    // Best Case & Goal Achievement
    setBestCaseScenarioText('');
    setGoalAchievementExperienceText('');

    // Best Case + Actions & Actions Certainty
    setActionsListText('');
    setActionsCertaintyWhyNotText('');

    // Detailed Action Process
    setActionFeelingInitial('');
    setActionFeelingDesired('');
    setActionCharacteristicsNeeded('');
    setActionCharacteristicsWithout('');
    setActionCharacteristicsWith('');
    setActionUnderstandingNeeded('');
    setActionUnderstandingWithout('');
    setActionUnderstandingWith('');

    // ARC Triangle
    setArcAffinityMyRating(10);
    setArcAffinityGoalRating(10);
    setArcAffinityWhyNot('');
    setArcAffinityExperienceInstead('');
    setArcAffinityRealizations('');
    setArcRealityMyAgreementRating(10);
    setArcRealityFateAgreementRating(10);
    setArcRealityWhyNot('');
    setArcRealityExperienceInstead('');
    setArcRealityRealizations('');
    setArcCommunicationOutflowRating(10);
    setArcCommunicationInflowRating(10);
    setArcCommunicationWhyNot('');
    setArcCommunicationExperienceInstead('');
    setArcCommunicationRealizations('');

    // EMT Process
    setEmtExperienceDescription('');
    setEmtInitialRating(0);
    setEmtPositiveParts('');
    setEmtNegativeParts('');
    setEmtNeedsDifferentBetter('');
    setEmtModifiedRating(0);
    setEmtNeedsDifferentEvenBetter('');
    setEmtHappenedBefore('');
    setEmtExperienceNow('');
    setEmtFinalRating(0);

    // Common
    setInsightsText('');
    setGoalImprovementText('');
    setSuggestedKeywords(null);
    
    if (!keepProcessSelection) {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  }

  const renderReflectionSection = () => (
    <>
        <Card className="shadow-lg rounded-xl overflow-hidden">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-foreground flex items-center">
                    <MessageSquareHeart className="mr-2 h-6 w-6 text-primary"/> Insights and Realizations
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                    Is there something you could conclude about this process, about possibilities for its achievement? (Usually something about how you could resolve that barrier). This is the user&apos;s most important part!
                </CardDescription>
            </CardHeader>
            <CardContent className="pt-6 space-y-2">
                <Label htmlFor="insightsText" className="text-base">Your General Insights:</Label>
                <Textarea
                    id="insightsText"
                    value={insightsText}
                    onChange={(e) => {
                      setInsightsText(e.target.value);
                      setSuggestedKeywords(null); 
                    }}
                    placeholder="e.g., I realize I can break down the cost, or seek alternative cheaper solutions..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                />
                <div className="mt-4 space-y-3">
                    <Button
                        onClick={handleSuggestKeywords}
                        disabled={isSuggestingKeywords || !insightsText.trim()}
                        variant="outline"
                        size="sm"
                        className="text-sm"
                    >
                        {isSuggestingKeywords ? (
                            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        ) : (
                            <Sparkles className="mr-2 h-4 w-4" />
                        )}
                        Suggest Keywords for General Insights
                    </Button>
                    {suggestedKeywords && suggestedKeywords.length > 0 && (
                        <div className="p-3 bg-muted/50 rounded-md border">
                            <h5 className="text-sm font-semibold mb-2 text-foreground">Suggested Keywords:</h5>
                            <div className="flex flex-wrap gap-2">
                                {suggestedKeywords.map((keyword, index) => (
                                    <Badge key={index} variant="secondary" className="text-xs">
                                        {keyword}
                                    </Badge>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </CardContent>
        </Card>

        <Card className="shadow-lg rounded-xl overflow-hidden">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-foreground flex items-center">
                    <Target className="mr-2 h-6 w-6 text-primary"/> Goal Improvement
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                    Does the process you did suggest some improvements or better definition of your goal? Should you add something, remove something or change something to make it more attractive and better defined?
                </CardDescription>
            </CardHeader>
            <CardContent className="pt-6 space-y-2">
                <Label htmlFor="goalImprovementText" className="text-base">Your Goal Improvements:</Label>
                <Textarea
                    id="goalImprovementText"
                    value={goalImprovementText}
                    onChange={(e) => setGoalImprovementText(e.target.value)}
                    placeholder="e.g., I could redefine my goal to be more specific about the timeline, or add smaller milestones..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                />
            </CardContent>
        </Card>

        <Card className="shadow-lg rounded-xl overflow-hidden border-primary">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-primary-foreground flex items-center">
                    <RefreshCw className="mr-2 h-6 w-6 text-primary"/> Repeat the Process
                </CardTitle>
            </CardHeader>
            <CardContent className="pt-2 space-y-3">
                <p className="text-base text-foreground">
                    Return to point 1. Identification of the next barrier which makes you retreat from your goal, and repeat the process until you know it is impossible to fail.
                </p>
                 <p className="text-sm text-muted-foreground">
                    If applicable to the current process, repeat steps until the desired outcome (e.g., 10/10 rating) is achieved. Usually this takes students 3-7 processes to achieve for the &quot;Impossible to Fail&quot; process.
                </p>
                 <Button 
                    variant={'outline'} 
                    onClick={() => {
                        resetAllFields();
                        toast({
                            title: "Process Reset",
                            description: "You can now select a new process or re-evaluate the current one from the start.",
                        });
                    }}
                    className="mt-2"
                >
                    <RefreshCw className="mr-2 h-4 w-4" /> Start New Process Iteration
                </Button>
            </CardContent>
        </Card>
    </>
  );

  const isSaveButtonDisabled = () => {
    if (isLoadingAi || selectedProcessType === null) return true;

    const noGeneralInputs = !insightsText.trim() && !goalImprovementText.trim();

    if (selectedProcessType === 'impossibleToFail') {
        if (canGoalFail === null) return true; 
        if (canGoalFail === true) { 
            if (!showProcessingResults && noGeneralInputs) return true;
            if (showProcessingResults && noGeneralInputs) return true; 
        }
        return false; 
    }

    if (selectedProcessType === 'goalDesirability') {
        if (goalWantRating < 10) {
            if (!whyNotTenRatingReason.trim() || !goalWantReasons.trim()) return true;
            if (noGeneralInputs && !whyNotTenRatingReason.trim() && !goalWantReasons.trim()) return true;
        }
         if (noGeneralInputs && goalWantRating === 10) return false; 
         if (goalWantRating < 10 && (!whyNotTenRatingReason.trim() || !goalWantReasons.trim())) return true;
         if (goalWantRating < 10 && (whyNotTenRatingReason.trim() || goalWantReasons.trim()) && noGeneralInputs) return true;
        return false;
    }

    if (selectedProcessType === 'bestCaseScenario') {
        if (!bestCaseScenarioText.trim() && noGeneralInputs) return true;
         if (!bestCaseScenarioText.trim()) return true; // Must have best case text
        return false;
    }

    if (selectedProcessType === 'goalAchievementExperience') {
        if (!goalAchievementExperienceText.trim() && noGeneralInputs) return true;
        if (!goalAchievementExperienceText.trim()) return true; // Must have achievement text
        return false;
    }

    if (selectedProcessType === 'bestCasePlusActions') {
        if ((!bestCaseScenarioText.trim() || !actionsListText.trim() || !actionsCertaintyWhyNotText.trim()) && noGeneralInputs ) return true;
        if (!bestCaseScenarioText.trim() || !actionsListText.trim() || !actionsCertaintyWhyNotText.trim()) return true;
        return false;
    }

    if (selectedProcessType === 'actionsCertainty') {
        if (!actionsCertaintyWhyNotText.trim() && noGeneralInputs) return true;
        if (!actionsCertaintyWhyNotText.trim()) return true;
        return false;
    }
    
    if (selectedProcessType === 'detailedActionProcess') {
        const noDetailedInputs = !actionFeelingInitial.trim() && !actionFeelingDesired.trim() && !actionCharacteristicsNeeded.trim() && !actionCharacteristicsWithout.trim() && !actionCharacteristicsWith.trim() && !actionUnderstandingNeeded.trim() && !actionUnderstandingWithout.trim() && !actionUnderstandingWith.trim();
        if (noDetailedInputs && noGeneralInputs) return true;
        return false;
    }

    if (selectedProcessType === 'arcTriangle') {
        const noArcInputs = 
            !arcAffinityWhyNot.trim() && !arcAffinityExperienceInstead.trim() && !arcAffinityRealizations.trim() &&
            !arcRealityWhyNot.trim() && !arcRealityExperienceInstead.trim() && !arcRealityRealizations.trim() &&
            !arcCommunicationWhyNot.trim() && !arcCommunicationExperienceInstead.trim() && !arcCommunicationRealizations.trim();
        
        if ((arcAffinityMyRating < 10 || arcAffinityGoalRating < 10) && !arcAffinityWhyNot.trim()) return true;
        if ((arcRealityMyAgreementRating < 10 || arcRealityFateAgreementRating < 10) && !arcRealityWhyNot.trim()) return true;
        if ((arcCommunicationOutflowRating < 10 || arcCommunicationInflowRating < 10) && !arcCommunicationWhyNot.trim()) return true;

        if (noArcInputs && noGeneralInputs) return true;
        
        if (arcAffinityRealizations.trim() || arcRealityRealizations.trim() || arcCommunicationRealizations.trim()) return false;
        if (insightsText.trim() || goalImprovementText.trim()) return false;
        if (arcAffinityMyRating === 10 && arcAffinityGoalRating === 10 &&
            arcRealityMyAgreementRating === 10 && arcRealityFateAgreementRating === 10 &&
            arcCommunicationOutflowRating === 10 && arcCommunicationInflowRating === 10 &&
            noArcInputs && noGeneralInputs) return false; // Allow save if all ratings 10 and no text input

        const affinityFilled = arcAffinityWhyNot.trim() || arcAffinityExperienceInstead.trim();
        const realityFilled = arcRealityWhyNot.trim() || arcRealityExperienceInstead.trim();
        const communicationFilled = arcCommunicationWhyNot.trim() || arcCommunicationExperienceInstead.trim();

        if (affinityFilled && !arcAffinityRealizations.trim() && noGeneralInputs) return true;
        if (realityFilled && !arcRealityRealizations.trim() && noGeneralInputs) return true;
        if (communicationFilled && !arcCommunicationRealizations.trim() && noGeneralInputs) return true;
        
        return false;
    }

    if (selectedProcessType === 'emt') {
        if (!emtExperienceDescription.trim()) return true; // EMT must have at least description
        if (!emtExperienceDescription.trim() && noGeneralInputs) return true;
        if (emtExperienceDescription.trim() && noGeneralInputs && !emtPositiveParts.trim() && !emtNegativeParts.trim()) return true; 
        return false;
    }

    return true; 
  };

  const getSaveButtonText = () => {
    return "Ok";
  };


  return (
    <div className="container mx-auto max-w-3xl py-8 px-4 flex flex-col gap-8">
      {selectedProcessType === null ? (
        <Card className="shadow-lg rounded-xl overflow-hidden">
          <CardHeader className="bg-card">
            <CardTitle className="text-2xl font-semibold text-foreground flex items-center">
              <ListChecks className="mr-3 h-7 w-7 text-primary"/>Choose Your Focus
            </CardTitle>
            <CardDescription className="text-base text-muted-foreground pt-2">
              Select which aspect of your goal you&apos;d like to process. You can switch or repeat processes as needed.
            </CardDescription>
          </CardHeader>
          <CardContent className="pt-6 grid grid-cols-1 sm:grid-cols-1 gap-4">
            <Button 
              onClick={() => { setSelectedProcessType('impossibleToFail'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <ShieldCheck className="mr-3 h-6 w-6 text-accent" /> Impossible to Fail
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('goalDesirability'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Heart className="mr-3 h-6 w-6 text-red-500" /> Goal Desirability
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('bestCaseScenario'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Sun className="mr-3 h-6 w-6 text-yellow-500" /> Best Case Scenario
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('goalAchievementExperience'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Award className="mr-3 h-6 w-6 text-blue-500" /> Goal Achievement Experience
            </Button>
             <Button 
              onClick={() => { setSelectedProcessType('bestCasePlusActions'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <ListOrdered className="mr-3 h-6 w-6 text-indigo-500" /> Best Case + Actions
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('actionsCertainty'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <HelpCircle className="mr-3 h-6 w-6 text-purple-500" /> Actions Certainty
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('detailedActionProcess'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Workflow className="mr-3 h-6 w-6 text-pink-500" /> Detailed Action Analysis
            </Button>
             <Button 
              onClick={() => { setSelectedProcessType('arcTriangle'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Triangle className="mr-3 h-6 w-6 text-green-500" /> ARC Triangle
            </Button>
            <Button 
              onClick={() => { setSelectedProcessType('emt'); resetAllFields(true);}} 
              size="lg" 
              className="py-6 text-lg justify-start pl-4"
              variant="outline"
            >
              <Wand className="mr-3 h-6 w-6 text-orange-500" /> EMT
            </Button>
          </CardContent>
        </Card>
      ) : (
        <>
          <div className="flex justify-start mb-0">
            <Button variant="outline" onClick={() => resetAllFields()} size="sm">
              <ArrowLeft className="mr-2 h-4 w-4" /> Back to Process Selection
            </Button>
          </div>

          {/* Impossible to Fail Process */}
          {selectedProcessType === 'impossibleToFail' && (
            <>
              <Card className="shadow-lg rounded-xl overflow-hidden">
                <CardHeader className="bg-primary/30">
                  <CardTitle className="text-xl font-semibold text-primary-foreground">
                    Impossible to fail
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6 space-y-3">
                    <p className="text-foreground text-base">
                      Pay attention to your goal, is it possible to fail?
                    </p>
                    <div className="flex flex-wrap gap-4 pt-1">
                      <Button 
                        variant={canGoalFail === true ? 'default' : 'outline'} 
                        onClick={() => handleSetCanGoalFail(true)}
                        className="flex-grow sm:flex-grow-0"
                      >
                        <XCircle className="mr-2 h-5 w-5" /> Yes, failure is possible
                      </Button>
                      <Button 
                        variant={canGoalFail === false ? 'default' : 'outline'} 
                        onClick={() => handleSetCanGoalFail(false)}
                        className={cn(
                          "flex-grow sm:flex-grow-0",
                          canGoalFail === false && "!bg-accent !text-accent-foreground hover:!bg-accent/90"
                        )}
                      >
                        <CheckCircle className="mr-2 h-5 w-5" /> No, it&apos;s fail-proof
                      </Button>
                    </div>
                </CardContent>
              </Card>

              {canGoalFail === true && (
                <Card className="shadow-lg rounded-xl overflow-hidden">
                  <CardHeader>
                    <CardTitle className="text-xl font-semibold text-foreground">
                      Standard 10F Process
                    </CardTitle>
                    <CardDescription className="text-base text-muted-foreground pt-2">
                      If your goal could fail, describe how that might happen.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="pt-6 space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="failureReason" className="text-base">How could your goal potentially fail?</Label>
                      <Textarea
                        id="failureReason"
                        value={failureReason}
                        onChange={(e) => {
                          setFailureReason(e.target.value);
                          setShowProcessingResults(false); 
                          setCategorizedFailureOutput(null);
                        }}
                        placeholder="e.g., It is too expensive, I might lose motivation, unexpected events, I am lazy..."
                        rows={3}
                        className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                      />
                    </div>
                    <Button 
                      onClick={handleFailureProcessing} 
                      disabled={isProcessingFailure || !failureReason.trim()}
                      className="w-full sm:w-auto bg-accent text-accent-foreground hover:bg-accent/90 focus-visible:ring-ring px-6 py-3 text-base"
                      aria-label="Process Potential Failure Mode"
                    >
                      {isProcessingFailure ? (
                        <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                      ) : (
                        <Lightbulb className="mr-2 h-5 w-5" />
                      )}
                      Process Potential Failure
                    </Button>
                  </CardContent>
                </Card>
              )}

              {canGoalFail === false && (
                 <Card className="shadow-lg rounded-xl overflow-hidden border-accent">
                    <CardHeader>
                      <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                        <ShieldCheck className="mr-2 h-6 w-6 text-accent" /> Goal Confidence
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="pt-2">
                      <p className="text-base text-foreground">
                        Great! You now know your goal is impossible to fail.
                      </p>
                    </CardContent>
                  </Card>
              )}

              {showProcessingResults && canGoalFail === true && (
                <Card className="shadow-lg rounded-xl overflow-hidden">
                  <CardHeader>
                    <CardTitle className="text-xl font-semibold text-foreground">10F Processing Results</CardTitle>
                    <CardDescription className="text-base text-muted-foreground pt-2">
                      Reflect on these statements to strengthen your resolve.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="pt-6 space-y-6">
                    {categorizedFailureOutput?.category && (
                      <div className="p-3 bg-primary/10 rounded-md border border-primary/30 text-sm text-primary-foreground flex items-start gap-2">
                        <Info className="h-5 w-5 mt-0.5 shrink-0" />
                        <p><span className="font-semibold">Categorization:</span> {getCategoryDescription(categorizedFailureOutput.category)}</p>
                      </div>
                    )}
                    <h4 className="text-lg font-semibold text-foreground">Do the 10F processing:</h4>
                    <Card className="bg-card border-destructive/50">
                      <CardHeader>
                        <CardTitle className="text-md font-semibold text-destructive flex items-center">
                          <AlertTriangle className="mr-2 h-5 w-5 text-destructive" /> Left Hand Focus
                        </CardTitle>
                      </CardHeader>
                      <CardContent>
                        <p className="text-base text-foreground">{leftHandText}</p>
                      </CardContent>
                    </Card>
                    <Card className="bg-card border-accent/50">
                      <CardHeader>
                        <CardTitle className="text-md font-semibold text-accent-foreground flex items-center">
                          <Lightbulb className="mr-2 h-5 w-5 text-accent" /> Right Hand Affirmation
                        </CardTitle>
                      </CardHeader>
                      <CardContent>
                        <p className="text-base text-foreground">{rightHandText}</p>
                      </CardContent>
                    </Card>
                  </CardContent>
                </Card>
              )}
            </>
          )}

          {/* Goal Desirability Process */}
          {selectedProcessType === 'goalDesirability' && (
            <>
              <Card className="shadow-lg rounded-xl overflow-hidden">
                <CardHeader className="bg-secondary/30">
                  <CardTitle className="text-xl font-semibold text-secondary-foreground flex items-center">
                    <Heart className="mr-3 h-6 w-6"/>Goal Desirability
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6 space-y-6">
                  <div className="space-y-2">
                    <Label htmlFor="goalWantRating" className="text-base">From 0-10, how much do you want your goal?</Label>
                    <div className="flex items-center gap-4">
                      <Slider
                        id="goalWantRating"
                        min={0}
                        max={10}
                        step={1}
                        value={[goalWantRating]}
                        onValueChange={(value) => setGoalWantRating(value[0])}
                        className="flex-grow"
                        aria-label="Goal want rating slider"
                      />
                      <span className="text-base font-medium w-10 text-right text-foreground">{goalWantRating}</span>
                    </div>
                  </div>

                  {goalWantRating < 10 && (
                    <>
                      <div className="space-y-2">
                        <Label htmlFor="whyNotTenRatingReason" className="text-base">Why is it not 10/10?</Label>
                        <Textarea
                          id="whyNotTenRatingReason"
                          value={whyNotTenRatingReason}
                          onChange={(e) => setWhyNotTenRatingReason(e.target.value)}
                          placeholder="e.g., I have some doubts, it requires a lot of effort..."
                          rows={3}
                          className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="goalWantReasons" className="text-base">Reason(s) for wanting your goal (one or two):</Label>
                        <Textarea
                          id="goalWantReasons"
                          value={goalWantReasons}
                          onChange={(e) => setGoalWantReasons(e.target.value)}
                          placeholder="e.g., It will improve my health, It will bring me joy..."
                          rows={3}
                          className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                        />
                      </div>
                    </>
                  )}
                </CardContent>
              </Card>
              
              {goalWantRating === 10 && selectedProcessType === 'goalDesirability' && (
                <Card className="shadow-lg rounded-xl overflow-hidden border-accent">
                    <CardHeader>
                      <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                        <Star className="mr-2 h-6 w-6 text-accent" /> Goal Commitment
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="pt-2">
                      <p className="text-base text-foreground">
                        Great! You know you want your goal 100%!
                      </p>
                    </CardContent>
                  </Card>
              )}
            </>
          )}

          {/* Best Case Scenario Process */}
          {selectedProcessType === 'bestCaseScenario' && (
            <Card className="shadow-lg rounded-xl overflow-hidden">
              <CardHeader className="bg-accent/10">
                <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                  <Sun className="mr-3 h-6 w-6"/>Best Case Scenario
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                  Envision the most positive outcome for your goal. How does it look and feel?
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6 space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="bestCaseScenarioText" className="text-base">Describe your best case scenario for this goal:</Label>
                  <Textarea
                    id="bestCaseScenarioText"
                    value={bestCaseScenarioText}
                    onChange={(e) => setBestCaseScenarioText(e.target.value)}
                    placeholder="e.g., I achieve my goal effortlessly, new opportunities open up, I feel immense joy and satisfaction..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
              </CardContent>
            </Card>
          )}

          {/* Goal Achievement Experience Process */}
          {selectedProcessType === 'goalAchievementExperience' && (
            <Card className="shadow-lg rounded-xl overflow-hidden">
              <CardHeader className="bg-primary/10">
                <CardTitle className="text-xl font-semibold text-primary-foreground flex items-center">
                  <Award className="mr-3 h-6 w-6"/>Goal Achievement Experience
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                  Imagine you have successfully achieved your goal. Describe this experience in detail. What do you see, hear, feel?
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6 space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="goalAchievementExperienceText" className="text-base">Describe your goal achievement experience:</Label>
                  <Textarea
                    id="goalAchievementExperienceText"
                    value={goalAchievementExperienceText}
                    onChange={(e) => setGoalAchievementExperienceText(e.target.value)}
                    placeholder="e.g., I feel a wave of accomplishment and pride. I'm celebrating with friends and family. This opens doors to..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
              </CardContent>
            </Card>
          )}

          {/* Best Case Scenario + List of Actions Process */}
          {selectedProcessType === 'bestCasePlusActions' && (
            <>
              <Card className="shadow-lg rounded-xl overflow-hidden">
                <CardHeader className="bg-accent/10">
                  <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                    <ListOrdered className="mr-3 h-6 w-6"/>Best Case + Actions
                  </CardTitle>
                  <CardDescription className="text-base text-muted-foreground pt-2">
                    Envision your best outcome, list actions, and assess your certainty.
                  </CardDescription>
                </CardHeader>
                <CardContent className="pt-6 space-y-6">
                  <div className="space-y-2">
                    <Label htmlFor="bestCaseScenarioTextPlusActions" className="text-base">Describe your best case scenario for this goal:</Label>
                    <Textarea
                      id="bestCaseScenarioTextPlusActions"
                      value={bestCaseScenarioText}
                      onChange={(e) => setBestCaseScenarioText(e.target.value)}
                      placeholder="e.g., I achieve my goal effortlessly..."
                      rows={3}
                      className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="actionsListText" className="text-base">List the actions to take:</Label>
                    <Textarea
                      id="actionsListText"
                      value={actionsListText}
                      onChange={(e) => setActionsListText(e.target.value)}
                      placeholder="e.g., 1. Research options. 2. Make a plan. 3. Execute step 1..."
                      rows={4}
                      className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="actionsCertaintyWhyNotTextBPA" className="text-base">If I take all the actions included, am I absolutely certain my actions will get to my goal? Why not?</Label>
                    <Textarea
                      id="actionsCertaintyWhyNotTextBPA"
                      value={actionsCertaintyWhyNotText}
                      onChange={(e) => setActionsCertaintyWhyNotText(e.target.value)}
                      placeholder="e.g., External factors, unexpected delays, resource limitations..."
                      rows={3}
                      className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                    />
                  </div>
                </CardContent>
              </Card>
            </>
          )}

          {/* Actions Certainty Process */}
          {selectedProcessType === 'actionsCertainty' && (
            <Card className="shadow-lg rounded-xl overflow-hidden">
              <CardHeader className="bg-secondary/30">
                <CardTitle className="text-xl font-semibold text-secondary-foreground flex items-center">
                  <HelpCircle className="mr-3 h-6 w-6"/>Actions Certainty
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                  Evaluate your confidence in your planned actions.
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6 space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="actionsCertaintyWhyNotTextAC" className="text-base">If I take all the actions included, am I absolutely certain my actions will get to my goal? Why not?</Label>
                  <Textarea
                    id="actionsCertaintyWhyNotTextAC"
                    value={actionsCertaintyWhyNotText}
                    onChange={(e) => setActionsCertaintyWhyNotText(e.target.value)}
                    placeholder="e.g., Potential obstacles, skill gaps, market changes..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
              </CardContent>
            </Card>
          )}

          {/* Detailed Action Analysis Process */}
          {selectedProcessType === 'detailedActionProcess' && (
            <Card className="shadow-lg rounded-xl overflow-hidden">
              <CardHeader className="bg-primary/10">
                <CardTitle className="text-xl font-semibold text-primary-foreground flex items-center">
                  <Workflow className="mr-3 h-6 w-6"/>Detailed Action Analysis
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                  Deep dive into a specific action: feelings, characteristics, and understanding.
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6 space-y-6">
                <div className="space-y-2">
                  <Label htmlFor="actionFeelingInitial" className="text-base">How will I feel going into this action?</Label>
                  <Textarea
                    id="actionFeelingInitial"
                    value={actionFeelingInitial}
                    onChange={(e) => setActionFeelingInitial(e.target.value)}
                    placeholder="e.g., Anxious, excited, overwhelmed..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="actionFeelingDesired" className="text-base">How would I like to feel instead?</Label>
                  <Textarea
                    id="actionFeelingDesired"
                    value={actionFeelingDesired}
                    onChange={(e) => setActionFeelingDesired(e.target.value)}
                    placeholder="e.g., Confident, calm, focused..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                 <div className="space-y-2">
                  <Label htmlFor="actionCharacteristicsNeeded" className="text-base">What characteristic(s) will I need to perform this action optimally?</Label>
                  <Textarea
                    id="actionCharacteristicsNeeded"
                    value={actionCharacteristicsNeeded}
                    onChange={(e) => setActionCharacteristicsNeeded(e.target.value)}
                    placeholder="e.g., Discipline, creativity, patience..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="actionCharacteristicsWithout" className="text-base">How will it be like if I don&apos;t have this characteristic(s)?</Label>
                  <Textarea
                    id="actionCharacteristicsWithout"
                    value={actionCharacteristicsWithout}
                    onChange={(e) => setActionCharacteristicsWithout(e.target.value)}
                    placeholder="e.g., Struggle, make mistakes, give up..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="actionCharacteristicsWith" className="text-base">How will it be if I do?</Label>
                  <Textarea
                    id="actionCharacteristicsWith"
                    value={actionCharacteristicsWith}
                    onChange={(e) => setActionCharacteristicsWith(e.target.value)}
                    placeholder="e.g., Smooth progress, enjoy the process, achieve better results..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                 <div className="space-y-2">
                  <Label htmlFor="actionUnderstandingNeeded" className="text-base">What would I need to understand to perform this action optimally?</Label>
                  <Textarea
                    id="actionUnderstandingNeeded"
                    value={actionUnderstandingNeeded}
                    onChange={(e) => setActionUnderstandingNeeded(e.target.value)}
                    placeholder="e.g., The steps involved, potential challenges, the underlying principles..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="actionUnderstandingWithout" className="text-base">How would it be if I don&apos;t understand that?</Label>
                  <Textarea
                    id="actionUnderstandingWithout"
                    value={actionUnderstandingWithout}
                    onChange={(e) => setActionUnderstandingWithout(e.target.value)}
                    placeholder="e.g., Confusion, inefficiency, frustration..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="actionUnderstandingWith" className="text-base">How would it be if I did?</Label>
                  <Textarea
                    id="actionUnderstandingWith"
                    value={actionUnderstandingWith}
                    onChange={(e) => setActionUnderstandingWith(e.target.value)}
                    placeholder="e.g., Clarity, effectiveness, adaptability..."
                    rows={2}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                  />
                </div>
              </CardContent>
            </Card>
          )}

          {/* ARC Triangle Process */}
          {selectedProcessType === 'arcTriangle' && (
            <Card className="shadow-lg rounded-xl overflow-hidden">
              <CardHeader className="bg-muted/30">
                <CardTitle className="text-xl font-semibold text-foreground flex items-center">
                  <Triangle className="mr-3 h-6 w-6"/>ARC Triangle
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                  Explore Affinity, Reality, and Communication related to your goal.
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6">
                <Accordion type="multiple" className="w-full space-y-4">
                  {/* Affinity Section */}
                  <AccordionItem value="affinity" className="border bg-card rounded-lg shadow-sm">
                    <AccordionTrigger className="px-4 py-3 text-lg font-medium hover:bg-muted/50 rounded-t-lg">
                      <div className="flex items-center"> <Users className="mr-2 h-5 w-5 text-primary"/> Affinity </div>
                    </AccordionTrigger>
                    <AccordionContent className="px-4 pt-2 pb-4 space-y-6 border-t">
                      <div className="space-y-2">
                        <Label htmlFor="arcAffinityMyRating" className="text-base">How much do I love my goal? (0-10)</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcAffinityMyRating" min={0} max={10} step={1} value={[arcAffinityMyRating]} onValueChange={(v) => setArcAffinityMyRating(v[0])} />
                          <span className="text-base font-medium w-10 text-right">{arcAffinityMyRating}</span>
                        </div>
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="arcAffinityGoalRating" className="text-base">Does my goal love me? (0-10)</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcAffinityGoalRating" min={0} max={10} step={1} value={[arcAffinityGoalRating]} onValueChange={(v) => setArcAffinityGoalRating(v[0])} />
                          <span className="text-base font-medium w-10 text-right">{arcAffinityGoalRating}</span>
                        </div>
                      </div>
                      {(arcAffinityMyRating < 10 || arcAffinityGoalRating < 10) && (
                        <div className="space-y-2">
                          <Label htmlFor="arcAffinityWhyNot" className="text-base">Why is it not 10/10?</Label>
                          <Textarea id="arcAffinityWhyNot" value={arcAffinityWhyNot} onChange={(e) => setArcAffinityWhyNot(e.target.value)} placeholder="Explain any reservations or doubts..." rows={3} />
                        </div>
                      )}
                      <div className="space-y-2">
                        <Label htmlFor="arcAffinityExperienceInstead" className="text-base">What would I like to experience instead?</Label>
                        <Textarea id="arcAffinityExperienceInstead" value={arcAffinityExperienceInstead} onChange={(e) => setArcAffinityExperienceInstead(e.target.value)} placeholder="Describe the ideal affinity experience..." rows={3} />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="arcAffinityRealizations" className="text-base">Any realizations or conclusions about Affinity?</Label>
                        <Textarea id="arcAffinityRealizations" value={arcAffinityRealizations} onChange={(e) => setArcAffinityRealizations(e.target.value)} placeholder="Your insights on affinity..." rows={3} />
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Reality Section */}
                  <AccordionItem value="reality" className="border bg-card rounded-lg shadow-sm">
                    <AccordionTrigger className="px-4 py-3 text-lg font-medium hover:bg-muted/50 rounded-t-lg">
                       <div className="flex items-center"> <Eye className="mr-2 h-5 w-5 text-primary"/> Reality </div>
                    </AccordionTrigger>
                    <AccordionContent className="px-4 pt-2 pb-4 space-y-6 border-t">
                      <div className="space-y-2">
                        <Label htmlFor="arcRealityMyAgreementRating" className="text-base">Am I in complete agreement with my goal? (0-10)</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcRealityMyAgreementRating" min={0} max={10} step={1} value={[arcRealityMyAgreementRating]} onValueChange={(v) => setArcRealityMyAgreementRating(v[0])} />
                          <span className="text-base font-medium w-10 text-right">{arcRealityMyAgreementRating}</span>
                        </div>
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="arcRealityFateAgreementRating" className="text-base">Does fate/destiny agree with my goal? (0-10)</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcRealityFateAgreementRating" min={0} max={10} step={1} value={[arcRealityFateAgreementRating]} onValueChange={(v) => setArcRealityFateAgreementRating(v[0])} />
                           <span className="text-base font-medium w-10 text-right">{arcRealityFateAgreementRating}</span>
                        </div>
                      </div>
                      {(arcRealityMyAgreementRating < 10 || arcRealityFateAgreementRating < 10) && (
                        <div className="space-y-2">
                          <Label htmlFor="arcRealityWhyNot" className="text-base">Why not?</Label>
                          <Textarea id="arcRealityWhyNot" value={arcRealityWhyNot} onChange={(e) => setArcRealityWhyNot(e.target.value)} placeholder="Explain any disagreements or misalignments..." rows={3} />
                        </div>
                      )}
                      <div className="space-y-2">
                        <Label htmlFor="arcRealityExperienceInstead" className="text-base">What would I like to experience instead?</Label>
                        <Textarea id="arcRealityExperienceInstead" value={arcRealityExperienceInstead} onChange={(e) => setArcRealityExperienceInstead(e.target.value)} placeholder="Describe the ideal reality alignment..." rows={3} />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="arcRealityRealizations" className="text-base">Any realizations or conclusions about Reality?</Label>
                        <Textarea id="arcRealityRealizations" value={arcRealityRealizations} onChange={(e) => setArcRealityRealizations(e.target.value)} placeholder="Your insights on reality..." rows={3} />
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Communication Section */}
                  <AccordionItem value="communication" className="border bg-card rounded-lg shadow-sm">
                    <AccordionTrigger className="px-4 py-3 text-lg font-medium hover:bg-muted/50 rounded-t-lg">
                      <div className="flex items-center"> <MessagesSquare className="mr-2 h-5 w-5 text-primary"/> Communication </div>
                    </AccordionTrigger>
                    <AccordionContent className="px-4 pt-2 pb-4 space-y-6 border-t">
                      <div className="space-y-2">
                        <Label htmlFor="arcCommunicationOutflowRating" className="text-base">Is my communication to my goal (outflow) 10/10?</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcCommunicationOutflowRating" min={0} max={10} step={1} value={[arcCommunicationOutflowRating]} onValueChange={(v) => setArcCommunicationOutflowRating(v[0])} />
                          <span className="text-base font-medium w-10 text-right">{arcCommunicationOutflowRating}</span>
                        </div>
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="arcCommunicationInflowRating" className="text-base">Am I willing to accept any communication coming from my goal (inflow) 10/10?</Label>
                        <div className="flex items-center gap-4">
                          <Slider id="arcCommunicationInflowRating" min={0} max={10} step={1} value={[arcCommunicationInflowRating]} onValueChange={(v) => setArcCommunicationInflowRating(v[0])} />
                          <span className="text-base font-medium w-10 text-right">{arcCommunicationInflowRating}</span>
                        </div>
                      </div>
                      {(arcCommunicationOutflowRating < 10 || arcCommunicationInflowRating < 10) && (
                        <div className="space-y-2">
                          <Label htmlFor="arcCommunicationWhyNot" className="text-base">Why not?</Label>
                          <Textarea id="arcCommunicationWhyNot" value={arcCommunicationWhyNot} onChange={(e) => setArcCommunicationWhyNot(e.target.value)} placeholder="Explain any communication barriers..." rows={3} />
                        </div>
                      )}
                      <div className="space-y-2">
                        <Label htmlFor="arcCommunicationExperienceInstead" className="text-base">What would I like to experience instead?</Label>
                        <Textarea id="arcCommunicationExperienceInstead" value={arcCommunicationExperienceInstead} onChange={(e) => setArcCommunicationExperienceInstead(e.target.value)} placeholder="Describe the ideal communication flow..." rows={3} />
                      </div>
                       <div className="space-y-2">
                        <Label htmlFor="arcCommunicationRealizations" className="text-base">Any realizations or conclusions about Communication?</Label>
                        <Textarea id="arcCommunicationRealizations" value={arcCommunicationRealizations} onChange={(e) => setArcCommunicationRealizations(e.target.value)} placeholder="Your insights on communication..." rows={3} />
                      </div>
                    </AccordionContent>
                  </AccordionItem>
                </Accordion>
              </CardContent>
            </Card>
          )}

          {/* EMT Process */}
          {selectedProcessType === 'emt' && (
             <Card className="shadow-lg rounded-xl overflow-hidden">
                <CardHeader className="bg-accent/10">
                    <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                    <Wand className="mr-3 h-6 w-6"/>EMT
                    </CardTitle>
                    <CardDescription className="text-base text-muted-foreground pt-2">
                    Work through an experience using the Experience Modification Technique.
                    </CardDescription>
                </CardHeader>
                <CardContent className="pt-6 space-y-6">
                    {/* Step 1 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtExperienceDescription" className="text-base">1. Describe the experience:</Label>
                        <Textarea id="emtExperienceDescription" value={emtExperienceDescription} onChange={(e) => setEmtExperienceDescription(e.target.value)} placeholder="Describe the experience you want to work on..." rows={3}/>
                    </div>
                    {/* Step 2 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtInitialRating" className="text-base">2. What is the rating of that experience? (-10 to 10)</Label>
                        <div className="flex items-center gap-4">
                        <Slider id="emtInitialRating" min={-10} max={10} step={1} value={[emtInitialRating]} onValueChange={(v) => setEmtInitialRating(v[0])} />
                        <span className="text-base font-medium w-12 text-right">{emtInitialRating}</span>
                        </div>
                    </div>
                    {/* Step 3 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtPositiveParts" className="text-base">3. What is positive (or relatively positive) part about the experience?</Label>
                        <Textarea id="emtPositiveParts" value={emtPositiveParts} onChange={(e) => setEmtPositiveParts(e.target.value)} placeholder="Identify any positive aspects..." rows={3}/>
                    </div>
                    {/* Step 4 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtNegativeParts" className="text-base">4. What is negative parts about the experience?</Label>
                        <Textarea id="emtNegativeParts" value={emtNegativeParts} onChange={(e) => setEmtNegativeParts(e.target.value)} placeholder="Identify negative aspects..." rows={3}/>
                    </div>
                    {/* Step 5 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtNeedsDifferentBetter" className="text-base">5. What needs to be different in that experience to make it even better than that?</Label>
                        <Textarea id="emtNeedsDifferentBetter" value={emtNeedsDifferentBetter} onChange={(e) => setEmtNeedsDifferentBetter(e.target.value)} placeholder="How could it be improved?" rows={3}/>
                    </div>
                    {/* Step 6 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtModifiedRating" className="text-base">6. What is the rating of that experience now? (-10 to 10)</Label>
                         <div className="flex items-center gap-4">
                        <Slider id="emtModifiedRating" min={-10} max={10} step={1} value={[emtModifiedRating]} onValueChange={(v) => setEmtModifiedRating(v[0])} />
                        <span className="text-base font-medium w-12 text-right">{emtModifiedRating}</span>
                        </div>
                    </div>
                    {/* Step 7 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtNeedsDifferentEvenBetter" className="text-base">7. What else needs to be different in that experience in order to make it even better than that? (History alignment. For example if you can order an experience like ordering a pizza, how would that experience look like)?</Label>
                        <Textarea id="emtNeedsDifferentEvenBetter" value={emtNeedsDifferentEvenBetter} onChange={(e) => setEmtNeedsDifferentEvenBetter(e.target.value)} placeholder="Further improvements, consider history alignment..." rows={4}/>
                    </div>
                    {/* Step 8 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtHappenedBefore" className="text-base">8. What happened before that so in that moment you experience that?</Label>
                        <Textarea id="emtHappenedBefore" value={emtHappenedBefore} onChange={(e) => setEmtHappenedBefore(e.target.value)} placeholder="Describe preceding events or states..." rows={3}/>
                    </div>
                    {/* Step 9 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtExperienceNow" className="text-base">9. Describe the experience as if it is happening right now.</Label>
                        <Textarea id="emtExperienceNow" value={emtExperienceNow} onChange={(e) => setEmtExperienceNow(e.target.value)} placeholder="Present tense description..." rows={3}/>
                    </div>
                    {/* Step 10 */}
                    <div className="space-y-2">
                        <Label htmlFor="emtFinalRating" className="text-base">10. What is the rating of that experience now? (-10 to 10)</Label>
                        <div className="flex items-center gap-4">
                        <Slider id="emtFinalRating" min={-10} max={10} step={1} value={[emtFinalRating]} onValueChange={(v) => setEmtFinalRating(v[0])} />
                        <span className="text-base font-medium w-12 text-right">{emtFinalRating}</span>
                        </div>
                    </div>
                     <p className="text-sm text-muted-foreground pt-2">
                        11. Repeat step 7-10 until rating of experience becomes 10/10. (You can save progress and continue later by selecting this process again).
                    </p>
                </CardContent>
            </Card>
          )}
          
          {/* Common Reflection and Improvement Sections - Rendered if applicable */}
          {((selectedProcessType === 'impossibleToFail' && showProcessingResults && canGoalFail === true) || 
            (selectedProcessType === 'goalDesirability' && goalWantRating < 10 && (whyNotTenRatingReason.trim() || goalWantReasons.trim())) ||
            (selectedProcessType === 'bestCaseScenario' && bestCaseScenarioText.trim()) ||
            (selectedProcessType === 'goalAchievementExperience' && goalAchievementExperienceText.trim()) ||
            (selectedProcessType === 'bestCasePlusActions' && (bestCaseScenarioText.trim() || actionsListText.trim() || actionsCertaintyWhyNotText.trim())) ||
            (selectedProcessType === 'actionsCertainty' && actionsCertaintyWhyNotText.trim()) ||
            (selectedProcessType === 'detailedActionProcess' && (actionFeelingInitial.trim() || actionFeelingDesired.trim() || actionCharacteristicsNeeded.trim() || actionCharacteristicsWithout.trim() || actionCharacteristicsWith.trim() || actionUnderstandingNeeded.trim() || actionUnderstandingWithout.trim() || actionUnderstandingWith.trim())) ||
            (selectedProcessType === 'arcTriangle' && (arcAffinityRealizations.trim() || arcRealityRealizations.trim() || arcCommunicationRealizations.trim() || insightsText.trim() || goalImprovementText.trim() )) ||
            (selectedProcessType === 'emt' && (emtExperienceDescription.trim() || emtPositiveParts.trim() || emtNegativeParts.trim()))
            ) &&
            renderReflectionSection()
          }
      
          <div className="mt-6 flex justify-center">
            <Button 
                onClick={handleJournalSubmit} 
                disabled={isSaveButtonDisabled()}
                className="w-full sm:w-auto bg-primary text-primary-foreground hover:bg-primary/90 focus-visible:ring-ring focus-visible:ring-offset-2 focus-visible:ring-2 px-8 py-3 text-lg rounded-lg shadow-md transition-all duration-150 ease-in-out hover:shadow-lg"
                aria-label="Save Progress"
                size="lg"
            >
              {isLoadingAi ? (
                  <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              ) : (
                  <>
                      <Send className="mr-2 h-5 w-5" />
                      {getSaveButtonText()}
                  </>
              )}
            </Button>
          </div>
        </>
      )}


      <div className="mt-8 text-center">
        <Button variant="link" asChild>
          <Link href="/">Back to Main Journal Page</Link>
        </Button>
         <Button variant="link" asChild className="ml-4">
          <Link href="/history">View History</Link>
        </Button>
      </div>
    </div>
  );
}

